.favorite-container{

    min-height: 100vh;
    padding: 20px;
}
.no-favorites{
    width: 100%;
    text-align: center;
    padding: 0 20px;
    margin-top: 10%;
    height: 100vh;
}


#favorite-card{
    display: flex;
    margin: 20px ;
    border: none;
    padding: 20px;
    background-color: white;
    border-radius: 20px;
    box-shadow: 0px 2px 5px 0px #000000;
    height: 200px;
    animation: appear 1s;

}


#favorite-img{
    height: 160px;
    width: 180px;
    object-fit: cover;
    margin: 20px;
    border-radius: 20px ;
    margin: auto ;
}

.favorite-cardlink{
    display: flex;
} 